@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(https://raw.githubusercontent.com/fardinahosancse/bg_web/main/bcg.jpeg);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



*{margin:0;padding:0}s{font-family:"Kanit", sans-serif;background-image:linear-gradient(to right top, #051937, #004d7a, #008793, #00bf72, #a8eb12);color:#5900ff;display:flex;flex-direction:column;align-items:center;text-align:center;width:100%;height:100%;background-attachment:fixed}h1{margin:2rem 0;font-weight:400}main{display:flex;flex-direction:column;border:1px solid #9ad3bc;align-items:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}section{display:flex;width:70rem;margin-bottom:2rem}.button{border:1px solid #5900ff;text-transform:uppercase;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:0.3rem 1.5rem;background-color:#9ad3bc;border-color:#9ad3bc;color:#132743;font-weight:500;font-family:"Kanit", sans-serif;border-radius:0.25rem;font-size:1rem;margin-right:0.25rem}.button:hover{background-color:#2ec1ac;border-color:#2ec1ac;cursor:pointer}.button:focus{outline:none;border-color:#2ec1ac;background-color:#2ec1ac;color:#5900ff}.button.addTask{font-size:3rem;padding:0 0 0.4rem 0;border-radius:50%;width:2.5rem;height:2.5rem;line-height:1rem;margin-top:2rem}.button.moveTask{font-size:1.5rem;padding:0.5rem 0.5rem;margin-bottom:0;line-height:1rem}.button.delete{background-color:#ffa5a5;border-color:#ffa5a5}.button.delete:hover{background-color:#ff8282;border-color:#ff8282}@media only screen and (max-width: 75rem){section{width:100%;flex-direction:column}}

.statusLine{width:100%;margin:0 0.5rem}.statusLine h3{font-weight:500;margin:1rem 0}@media only screen and (max-width: 75rem){.statusLine{width:initial}}

body{background-image:url(https://raw.githubusercontent.com/fardinahosancse/bg_web/main/1.jpg)}.task{padding:1rem;margin-bottom:0.5rem;display:flex;align-items:flex-end;border:1px solid #5900ff;border-radius:0.25rem}.task.collapsedTask .moveTask{margin-bottom:0.5rem;font-size:1.5rem;padding:0.3rem 0.5rem}.task form{display:flex;flex-direction:column;text-align:center;align-items:center;margin:0 0.25rem}.task form .input{background-color:transparent;border:1px solid transparent;border-radius:0.25rem;color:#5900ff;text-align:center;resize:none;display:flex;font-family:"Kanit", sans-serif}.task form .input:hover{cursor:pointer;border:1px solid #5900ff}.task form .input:focus{outline:none;border-color:#5900ff;color:#5900ff}.task form .input::-webkit-input-placeholder{color:#5900ff}.task form .input::placeholder{color:#5900ff}.task form .title{margin-bottom:0.5rem;padding:0.5rem 0;width:100%;font-size:1.1rem}.task form .title:hover{cursor:text}.task form .description{margin-bottom:0.5rem;padding:0.5rem;width:100%;font-size:1.1rem}.task form .description:hover{cursor:text}.task form .urgencyLabels{margin-bottom:1.5rem;display:flex;flex-direction:row}.task form .urgencyLabels label{color:#132743;padding:0.05rem 0.5rem;margin:0 0.25rem;text-transform:uppercase;border-radius:0.25rem}.task form .urgencyLabels label .icon{font-size:1.25rem;margin-right:0.25rem;transition:all 0.4s ease-in-out}.task form .urgencyLabels label .icon.hide{display:none}.task form .urgencyLabels label .icon.show{display:inline-block}.task form .urgencyLabels label input{display:none}.task form .urgencyLabels label.low{background-color:#9ad3bc}.task form .urgencyLabels label.low:hover{cursor:pointer;background-color:#2ec1ac;border-color:#2ec1ac}.task form .urgencyLabels label.medium{background-color:#edeee1}.task form .urgencyLabels label.medium:hover{cursor:pointer;background-color:#2ec1ac;border-color:#2ec1ac}.task form .urgencyLabels label.high{background-color:#ffa5a5}.task form .urgencyLabels label.high:hover{cursor:pointer;background-color:#2ec1ac;border-color:#2ec1ac}.task form .urgencyLabels label.selected{font-weight:700}.task form.collapsed{flex-direction:row;align-content:center}.task form.collapsed .title{margin:0}.task form.collapsed .title:hover{border-color:transparent;cursor:default}.task form.collapsed .urgencyLabels{margin-bottom:0;margin-right:1rem}.task form.collapsed .description,.task form.collapsed label{display:none}.task form.collapsed label.selected{display:inline-block;font-weight:500}.task form.collapsed .button{padding:0.025rem 0.5rem}.task form.collapsed .button.moveTask{background-color:red}.task form.collapsed:hover .button{display:inline-block}


